header {
  padding: 30px 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
  width: 100%;
}
header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .container .page--logo {
  position: relative;
  z-index: 31;
}
header.scrolled {
  background-color: #ffffff;
  box-shadow: 0 3px 20px rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}
header ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}
header ul li {
  transition: 0.2s ease;
}
header ul li:hover {
  color: #141720;
  transform: scale(1.3);
}
header .hamburger {
  display: none;
  cursor: pointer;
}
header .hamburger .bar {
  display: block;
  width: 25px;
  height: 3px;
  margin-bottom: 5px;
  transition: all 300ms ease;
  background-color: #141720;
}
header .hamburger .bar:last-child {
  margin-bottom: 0;
}

